<template>
  <v-card
    v-if="!editable"
    elevation="0"
    class="quote-block"
    :style="{ borderRadius: '16px', borderLeft: '4px solid primary' }"
    @click="$emit('edit')"
  >
    <v-card-text>
      <div class="text-body-1 font-italic">
        "{{ block.content || '' }}"
      </div>
      <div v-if="block.author" class="text-caption text-grey mt-2">
         {{ block.author }}
      </div>
    </v-card-text>
  </v-card>

  <v-card v-else elevation="0" variant="outlined" :style="{ borderRadius: '16px' }">
    <v-card-text>
      <v-textarea
        v-model="localContent"
        variant="outlined"
        auto-grow
        rows="2"
        label=""
        hide-details
      />
      <v-text-field
        v-model="localAuthor"
        variant="outlined"
        density="compact"
        label=" ()"
        hide-details
        class="mt-2"
      />
      <div class="d-flex justify-end mt-2">
        <v-btn size="small" color="primary" @click="handleSave">
          
        </v-btn>
      </div>
    </v-card-text>
  </v-card>
</template>

<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  block: {
    type: Object,
    required: true
  },
  editable: {
    type: Boolean,
    default: false
  }
});

const emit = defineEmits(['edit', 'save', 'delete']);

const localContent = ref(props.block.content || '');
const localAuthor = ref(props.block.author || '');

watch(() => props.block.content, (newVal) => {
  localContent.value = newVal || '';
});

watch(() => props.block.author, (newVal) => {
  localAuthor.value = newVal || '';
});

const handleSave = () => {
  emit('save', {
    content: localContent.value,
    author: localAuthor.value
  });
};
</script>

<style scoped>
.quote-block {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.02);
}
</style>
