<template>
  <div class="subscription">
    <!-- Header -->
    <v-app-bar
      elevation="0"
      color="transparent"
      class="px-4"
    >
      <v-btn
        icon
        variant="text"
        @click="$router.push('/')"
      >
        <v-icon>mdi-arrow-left</v-icon>
      </v-btn>
      <v-app-bar-title></v-app-bar-title>
    </v-app-bar>

    <v-container class="py-4">
      <!-- Current Plan -->
      <v-card
        elevation="0"
        class="mb-4"
        :style="{ borderRadius: '24px' }"
      >
        <v-card-title class="text-h6">
           
        </v-card-title>
        <v-card-text>
          <div class="d-flex align-center justify-space-between">
            <div>
              <div class="text-h5 font-weight-bold">
                {{ subscriptionStore.subscription?.planName || 'FREE' }}
              </div>
              <div v-if="subscriptionStore.isPremium && subscriptionStore.subscription?.endDate" class="text-caption text-grey mt-1">
                 {{ formatDate(subscriptionStore.subscription.endDate) }}
              </div>
            </div>
            <v-chip
              :color="subscriptionStore.isPremium ? 'success' : 'grey'"
              size="large"
            >
              {{ subscriptionStore.isPremium ? 'PREMIUM' : 'FREE' }}
            </v-chip>
          </div>
        </v-card-text>
      </v-card>

      <!-- Usage Limits -->
      <v-card
        elevation="0"
        class="mb-4"
        :style="{ borderRadius: '24px' }"
      >
        <v-card-title class="text-h6">
           
        </v-card-title>
        <v-card-text>
          <div class="mb-4">
            <div class="d-flex justify-space-between mb-2">
              <span class="text-body-2"></span>
              <span class="text-body-2">
                {{ subscriptionStore.currentPages }} / {{ subscriptionStore.maxPages }}
              </span>
            </div>
            <v-progress-linear
              :model-value="(subscriptionStore.currentPages / subscriptionStore.maxPages) * 100"
              :color="subscriptionStore.pagesLimitReached ? 'error' : 'primary'"
              height="8"
              rounded
            />
            <div v-if="subscriptionStore.pagesLimitReached" class="text-caption text-error mt-1">
                
            </div>
          </div>

          <div>
            <div class="d-flex justify-space-between mb-2">
              <span class="text-body-2">  </span>
              <span class="text-body-2">
                . {{ subscriptionStore.maxBlocksPerPage }}
              </span>
            </div>
          </div>
        </v-card-text>
      </v-card>

      <!-- Plans -->
      <v-card
        v-if="subscriptionStore.canUpgrade"
        elevation="0"
        :style="{ borderRadius: '24px' }"
      >
        <v-card-title class="text-h6">
           
        </v-card-title>
        <v-card-text>
          <div class="plans-list">
            <v-card
              v-for="plan in subscriptionStore.plans"
              :key="plan.id"
              elevation="0"
              class="plan-card mb-3"
              :style="{ borderRadius: '16px', border: plan.recommended ? '2px solid primary' : '1px solid rgba(0,0,0,0.1)' }"
              @click="selectPlan(plan)"
            >
              <v-card-text>
                <div class="d-flex justify-space-between align-center mb-2">
                  <div>
                    <div class="text-h6 font-weight-bold">{{ plan.name }}</div>
                    <div class="text-h4 font-weight-bold text-primary mt-2">
                      {{ plan.price }} 
                    </div>
                  </div>
                  <v-chip v-if="plan.recommended" color="primary" size="small">
                    
                  </v-chip>
                </div>
                <div class="text-body-2 text-grey mb-2">
                  {{ plan.durationDays }} 
                </div>
                <v-list density="compact" class="pa-0">
                  <v-list-item
                    v-for="benefit in plan.benefits"
                    :key="benefit"
                    class="pa-0"
                  >
                    <template #prepend>
                      <v-icon size="small" color="success">mdi-check</v-icon>
                    </template>
                    <v-list-item-title class="text-body-2">
                      {{ benefit }}
                    </v-list-item-title>
                  </v-list-item>
                </v-list>
                <v-btn
                  block
                  color="primary"
                  class="mt-3"
                  @click.stop="selectPlan(plan)"
                >
                  
                </v-btn>
              </v-card-text>
            </v-card>
          </div>
        </v-card-text>
      </v-card>
    </v-container>

    <!-- Payment Success Modal -->
    <PaymentSuccessModal
      v-model="showPaymentSuccess"
      @close="handlePaymentSuccess"
    />
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import { useSubscriptionStore } from '../stores/subscription';
import PaymentSuccessModal from '../components/modals/PaymentSuccessModal.vue';

const subscriptionStore = useSubscriptionStore();
const showPaymentSuccess = ref(false);

const formatDate = (dateString) => {
  if (!dateString) return '';
  return new Date(dateString).toLocaleDateString('ru-RU');
};

const selectPlan = async (plan) => {
  try {
    const response = await subscriptionStore.createInvoice(plan.id);
    if (response.invoiceLink) {
      //    Telegram
      if (window.Telegram?.WebApp?.openInvoice) {
        window.Telegram.WebApp.openInvoice(response.invoiceLink, (status) => {
          if (status === 'paid') {
            showPaymentSuccess.value = true;
            subscriptionStore.fetchSubscription();
            subscriptionStore.fetchLimits();
          }
        });
      } else {
        // Fallback -    
        window.open(response.invoiceLink, '_blank');
      }
    }
  } catch (error) {
    console.error('   :', error);
  }
};

const handlePaymentSuccess = () => {
  showPaymentSuccess.value = false;
};

onMounted(async () => {
  await subscriptionStore.fetchSubscription();
  await subscriptionStore.fetchLimits();
  await subscriptionStore.fetchPlans();
});
</script>

<style scoped>
.subscription {
  min-height: 100vh;
  background-color: var(--tg-theme-bg-color, #F8F9FA);
}

.plans-list {
  max-height: 60vh;
  overflow-y: auto;
}

.plan-card {
  cursor: pointer;
  transition: transform 0.2s;
}

.plan-card:hover {
  transform: translateY(-2px);
}
</style>
