<template>
  <div class="dashboard">
    <!-- Header -->
    <v-app-bar
      elevation="0"
      color="transparent"
      class="px-4"
    >
      <v-text-field
        v-model="searchQuery"
        placeholder=" ..."
        prepend-inner-icon="mdi-magnify"
        variant="solo-filled"
        density="compact"
        hide-details
        class="search-field"
        @input="handleSearch"
      />
      <v-btn
        icon
        variant="text"
        @click="$router.push('/subscription')"
      >
        <v-icon>mdi-account-circle</v-icon>
      </v-btn>
    </v-app-bar>

    <!-- Main Content -->
    <v-container class="py-4">
      <!-- Empty State -->
      <div v-if="!pagesStore.loading && pagesStore.isEmpty" class="empty-state">
        <v-icon size="80" color="grey-lighten-1">mdi-note-text-outline</v-icon>
        <h2 class="text-h6 mt-4 mb-2">  </h2>
        <p class="text-body-2 text-grey">   </p>
        <v-btn
          color="primary"
          size="large"
          class="mt-4"
          @click="openCreateModal"
        >
            
        </v-btn>
      </div>

      <!-- Masonry Grid -->
      <div v-else class="masonry-grid">
        <div
          v-for="page in pagesStore.pages"
          :key="page.id"
          class="page-card"
          @click="openPage(page.id)"
        >
          <v-card
            elevation="0"
            class="page-card-content"
            :style="{ borderRadius: '24px' }"
          >
            <v-card-title class="text-h6 pb-2">
              {{ page.title || ' ' }}
            </v-card-title>
            
            <v-card-text class="pt-0">
              <!-- Preview  2  -->
              <div v-if="page.blocks && page.blocks.length > 0" class="preview">
                <div
                  v-for="block in page.blocks.slice(0, 2)"
                  :key="block.id"
                  class="preview-block"
                >
                  <component
                    :is="getBlockComponent(block.type)"
                    :block="block"
                    :preview="true"
                  />
                </div>
              </div>
              <div v-else class="text-grey text-body-2">
                 
              </div>
              
              <div class="mt-2 text-caption text-grey">
                {{ formatDate(page.updatedAt) }}
              </div>
            </v-card-text>
          </v-card>
        </div>
      </div>

      <!-- Loading -->
      <div v-if="pagesStore.loading" class="text-center py-8">
        <v-progress-circular
          indeterminate
          color="primary"
        />
      </div>
    </v-container>

    <!-- FAB -->
    <v-btn
      class="fab-button"
      size="large"
      color="primary"
      icon="mdi-plus"
      @click="openCreateModal"
    />

    <!-- Create Page Modal -->
    <CreatePageModal
      v-model="showCreateModal"
      @created="handlePageCreated"
    />
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import { usePagesStore } from '../stores/pages';
import { debounce } from '../utils/debounce';
import CreatePageModal from '../components/modals/CreatePageModal.vue';
import TextBlock from '../components/blocks/TextBlock.vue';
import ChecklistBlock from '../components/blocks/ChecklistBlock.vue';

const router = useRouter();
const pagesStore = usePagesStore();

const searchQuery = ref('');
const showCreateModal = ref(false);

const handleSearch = debounce((event) => {
  const query = event.target.value.trim();
  if (query) {
    pagesStore.searchPages(query);
  } else {
    pagesStore.fetchPages();
  }
}, 300);

const openPage = (pageId) => {
  router.push(`/pages/${pageId}`);
};

const openCreateModal = () => {
  showCreateModal.value = true;
};

const handlePageCreated = (page) => {
  router.push(`/pages/${page.id}`);
};

const formatDate = (dateString) => {
  if (!dateString) return '';
  const date = new Date(dateString);
  const now = new Date();
  const diffMs = now - date;
  const diffMins = Math.floor(diffMs / 60000);
  const diffHours = Math.floor(diffMs / 3600000);
  const diffDays = Math.floor(diffMs / 86400000);

  if (diffMins < 1) return ' ';
  if (diffMins < 60) return `${diffMins} . `;
  if (diffHours < 24) return `${diffHours} . `;
  if (diffDays < 7) return `${diffDays} . `;
  return date.toLocaleDateString('ru-RU');
};

const getBlockComponent = (blockType) => {
  const components = {
    TEXT: TextBlock,
    CHECKLIST: ChecklistBlock
  };
  return components[blockType] || TextBlock;
};

onMounted(() => {
  pagesStore.fetchPages();
});
</script>

