<template>
  <v-card
    v-if="!editable"
    elevation="0"
    class="text-block"
    :style="{ borderRadius: '16px' }"
    @click="$emit('edit')"
  >
    <v-card-text>
      <div v-if="preview" class="text-body-2">
        {{ block.content || '' }}
      </div>
      <div v-else class="text-body-1">
        {{ block.content || ' ' }}
      </div>
    </v-card-text>
  </v-card>

  <v-textarea
    v-else
    v-model="localContent"
    variant="outlined"
    auto-grow
    rows="2"
    hide-details
    @blur="handleSave"
    @keydown.ctrl.enter="handleSave"
  />
</template>

<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  block: {
    type: Object,
    required: true
  },
  editable: {
    type: Boolean,
    default: false
  },
  preview: {
    type: Boolean,
    default: false
  }
});

const emit = defineEmits(['edit', 'save', 'delete']);

const localContent = ref(props.block.content || '');

watch(() => props.block.content, (newVal) => {
  localContent.value = newVal || '';
});

const handleSave = () => {
  emit('save', {
    content: localContent.value
  });
};
</script>

<style scoped>
.text-block {
  cursor: pointer;
  transition: background-color 0.2s;
}

.text-block:hover {
  background-color: rgba(0, 0, 0, 0.02);
}
</style>
