<template>
  <v-card
    v-if="!editable"
    elevation="0"
    class="pros-cons-block"
    :style="{ borderRadius: '16px' }"
    @click="$emit('edit')"
  >
    <v-card-text>
      <div class="d-flex">
        <div class="flex-1 mr-2">
          <div class="text-caption font-weight-bold text-success mb-2"></div>
          <ul v-if="pros.length > 0" class="pros-list">
            <li v-for="(item, idx) in pros" :key="idx" class="text-body-2">
              {{ item }}
            </li>
          </ul>
          <div v-else class="text-body-2 text-grey"> </div>
        </div>
        <v-divider vertical />
        <div class="flex-1 ml-2">
          <div class="text-caption font-weight-bold text-error mb-2"></div>
          <ul v-if="cons.length > 0" class="cons-list">
            <li v-for="(item, idx) in cons" :key="idx" class="text-body-2">
              {{ item }}
            </li>
          </ul>
          <div v-else class="text-body-2 text-grey"> </div>
        </div>
      </div>
    </v-card-text>
  </v-card>

  <v-card v-else elevation="0" variant="outlined" :style="{ borderRadius: '16px' }">
    <v-card-text>
      <div class="d-flex">
        <div class="flex-1 mr-2">
          <div class="text-caption font-weight-bold text-success mb-2"></div>
          <div
            v-for="(item, idx) in localPros"
            :key="idx"
            class="item-edit-row"
          >
            <v-text-field
              v-model="localPros[idx]"
              variant="plain"
              density="compact"
              placeholder=""
              hide-details
            />
            <v-btn
              icon
              size="small"
              variant="text"
              @click="removePros(idx)"
            >
              <v-icon size="small">mdi-close</v-icon>
            </v-btn>
          </div>
          <v-btn
            block
            variant="text"
            size="small"
            prepend-icon="mdi-plus"
            @click="addPros"
          >
            
          </v-btn>
        </div>
        <v-divider vertical />
        <div class="flex-1 ml-2">
          <div class="text-caption font-weight-bold text-error mb-2"></div>
          <div
            v-for="(item, idx) in localCons"
            :key="idx"
            class="item-edit-row"
          >
            <v-text-field
              v-model="localCons[idx]"
              variant="plain"
              density="compact"
              placeholder=""
              hide-details
            />
            <v-btn
              icon
              size="small"
              variant="text"
              @click="removeCons(idx)"
            >
              <v-icon size="small">mdi-close</v-icon>
            </v-btn>
          </div>
          <v-btn
            block
            variant="text"
            size="small"
            prepend-icon="mdi-plus"
            @click="addCons"
          >
            
          </v-btn>
        </div>
      </div>
      <div class="d-flex justify-end mt-2">
        <v-btn size="small" color="primary" @click="handleSave">
          
        </v-btn>
      </div>
    </v-card-text>
  </v-card>
</template>

<script setup>
import { ref, computed, watch } from 'vue';

const props = defineProps({
  block: {
    type: Object,
    required: true
  },
  editable: {
    type: Boolean,
    default: false
  }
});

const emit = defineEmits(['edit', 'save', 'delete']);

const isPros = computed(() => props.block.type === 'PROS');
const pros = computed(() => {
  if (isPros.value) {
    return props.block.items || [];
  }
  //   CONS   
  return [];
});

const cons = computed(() => {
  if (!isPros.value) {
    return props.block.items || [];
  }
  return [];
});

const localPros = ref([...pros.value]);
const localCons = ref([...cons.value]);

watch(() => props.block.items, (newItems) => {
  if (isPros.value) {
    localPros.value = newItems ? [...newItems] : [];
  } else {
    localCons.value = newItems ? [...newItems] : [];
  }
}, { deep: true });

const addPros = () => {
  localPros.value.push('');
};

const removePros = (idx) => {
  localPros.value.splice(idx, 1);
};

const addCons = () => {
  localCons.value.push('');
};

const removeCons = (idx) => {
  localCons.value.splice(idx, 1);
};

const handleSave = () => {
  if (isPros.value) {
    emit('save', {
      items: localPros.value.filter(item => item.trim())
    });
  } else {
    emit('save', {
      items: localCons.value.filter(item => item.trim())
    });
  }
};
</script>

<style scoped>
.pros-cons-block {
  cursor: pointer;
}

.pros-list,
.cons-list {
  padding-left: 20px;
  margin: 0;
}

.item-edit-row {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
}
</style>
