<template>
  <v-card
    v-if="!editable"
    elevation="0"
    class="key-value-block"
    :style="{ borderRadius: '16px' }"
    @click="$emit('edit')"
  >
    <v-card-text>
      <div
        v-for="(pair, idx) in pairs"
        :key="idx"
        class="key-value-pair"
      >
        <span class="font-weight-bold text-body-2">{{ pair.key }}:</span>
        <span class="text-body-1 ml-2">{{ pair.value }}</span>
      </div>
      <div v-if="pairs.length === 0" class="text-body-2 text-grey">
         
      </div>
    </v-card-text>
  </v-card>

  <v-card v-else elevation="0" variant="outlined" :style="{ borderRadius: '16px' }">
    <v-card-text>
      <div
        v-for="(pair, idx) in localPairs"
        :key="idx"
        class="pair-edit-row"
      >
        <v-text-field
          v-model="pair.key"
          variant="plain"
          density="compact"
          placeholder=""
          hide-details
        />
        <v-text-field
          v-model="pair.value"
          variant="plain"
          density="compact"
          placeholder=""
          hide-details
        />
        <v-btn
          icon
          size="small"
          variant="text"
          @click="removePair(idx)"
        >
          <v-icon size="small">mdi-close</v-icon>
        </v-btn>
      </div>
      <v-btn
        block
        variant="text"
        prepend-icon="mdi-plus"
        @click="addNewPair"
      >
         
      </v-btn>
      <div class="d-flex justify-end mt-2">
        <v-btn size="small" color="primary" @click="handleSave">
          
        </v-btn>
      </div>
    </v-card-text>
  </v-card>
</template>

<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  block: {
    type: Object,
    required: true
  },
  editable: {
    type: Boolean,
    default: false
  }
});

const emit = defineEmits(['edit', 'save', 'delete']);

const pairs = props.block.pairs || [];
const localPairs = ref(pairs.length > 0 ? [...pairs] : [{ key: '', value: '' }]);

watch(() => props.block.pairs, (newPairs) => {
  localPairs.value = newPairs && newPairs.length > 0 ? [...newPairs] : [{ key: '', value: '' }];
}, { deep: true });

const addNewPair = () => {
  localPairs.value.push({ key: '', value: '' });
};

const removePair = (idx) => {
  localPairs.value.splice(idx, 1);
};

const handleSave = () => {
  emit('save', {
    pairs: localPairs.value.filter(pair => pair.key.trim() && pair.value.trim())
  });
};
</script>

<style scoped>
.key-value-block {
  cursor: pointer;
}

.key-value-pair {
  margin-bottom: 8px;
}

.pair-edit-row {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
}
</style>
