<template>
  <v-dialog
    :model-value="modelValue"
    max-width="400"
    @update:model-value="$emit('update:modelValue', $event)"
  >
    <v-card :style="{ borderRadius: '24px' }">
      <v-card-title class="text-h6">
        {{ title }}
      </v-card-title>
      <v-card-text>
        <p class="text-body-1">
          {{ message }}
        </p>
        <p class="text-body-2 text-grey mt-2">
             .
        </p>
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <v-btn
          variant="text"
          @click="$emit('update:modelValue', false)"
        >
          
        </v-btn>
        <v-btn
          color="error"
          @click="handleConfirm"
        >
          
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script setup>
defineProps({
  modelValue: {
    type: Boolean,
    required: true
  },
  title: {
    type: String,
    default: ' '
  },
  message: {
    type: String,
    default: ' ,     ?'
  }
});

const emit = defineEmits(['update:modelValue', 'confirm']);

const handleConfirm = () => {
  emit('confirm');
  emit('update:modelValue', false);
};
</script>
