<template>
  <v-card
    v-if="!editable"
    elevation="0"
    class="idea-card-block"
    :style="{
      borderRadius: '16px',
      background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
      color: 'white'
    }"
    @click="$emit('edit')"
  >
    <v-card-text>
      <div class="d-flex align-center mb-2">
        <v-icon color="white">mdi-lightbulb-on</v-icon>
        <span class="ml-2 text-body-2 font-weight-bold"></span>
      </div>
      <div class="text-body-1">
        {{ block.content || ' ...' }}
      </div>
    </v-card-text>
  </v-card>

  <v-card
    v-else
    elevation="0"
    variant="outlined"
    :style="{ borderRadius: '16px' }"
  >
    <v-card-text>
      <v-textarea
        v-model="localContent"
        variant="outlined"
        auto-grow
        rows="3"
        hide-details
        placeholder="  ..."
      />
      <div class="d-flex justify-end mt-2">
        <v-btn
          size="small"
          color="primary"
          @click="handleSave"
        >
          
        </v-btn>
      </div>
    </v-card-text>
  </v-card>
</template>

<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  block: {
    type: Object,
    required: true
  },
  editable: {
    type: Boolean,
    default: false
  }
});

const emit = defineEmits(['edit', 'save', 'delete']);

const localContent = ref(props.block.content || '');

watch(() => props.block.content, (newVal) => {
  localContent.value = newVal || '';
});

const handleSave = () => {
  emit('save', {
    content: localContent.value
  });
};
</script>

<style scoped>
.idea-card-block {
  cursor: pointer;
  transition: transform 0.2s;
}

.idea-card-block:hover {
  transform: translateY(-2px);
}
</style>
