import { defineStore } from 'pinia';
import { subscriptionApi } from '../api/subscription';

export const useSubscriptionStore = defineStore('subscription', {
  state: () => ({
    subscription: null,
    limits: null,
    plans: [],
    history: [],
    loading: false,
    error: null
  }),

  getters: {
    isPremium: (state) => state.subscription?.plan === 'PREMIUM',
    isFree: (state) => state.subscription?.plan === 'FREE',
    canUpgrade: (state) => state.subscription?.canUpgrade || false,
    pagesLimitReached: (state) => state.limits?.pagesLimitReached || false,
    remainingPages: (state) => state.limits?.remainingPages || 0,
    maxPages: (state) => state.limits?.maxPages || 3,
    currentPages: (state) => state.limits?.currentPages || 0,
    maxBlocksPerPage: (state) => state.limits?.maxBlocksPerPage || 10
  },

  actions: {
    async fetchSubscription() {
      this.loading = true;
      this.error = null;
      try {
        const response = await subscriptionApi.getSubscription();
        this.subscription = response.data;
        return response.data;
      } catch (error) {
        this.error = error.message;
        throw error;
      } finally {
        this.loading = false;
      }
    },

    async fetchLimits() {
      this.loading = true;
      this.error = null;
      try {
        const response = await subscriptionApi.getLimits();
        this.limits = response.data;
        return response.data;
      } catch (error) {
        this.error = error.message;
        throw error;
      } finally {
        this.loading = false;
      }
    },

    async fetchPlans() {
      this.loading = true;
      this.error = null;
      try {
        const response = await subscriptionApi.getPlans();
        this.plans = response.data;
        return response.data;
      } catch (error) {
        this.error = error.message;
        throw error;
      } finally {
        this.loading = false;
      }
    },

    async fetchHistory() {
      this.loading = true;
      this.error = null;
      try {
        const response = await subscriptionApi.getHistory();
        this.history = response.data;
        return response.data;
      } catch (error) {
        this.error = error.message;
        throw error;
      } finally {
        this.loading = false;
      }
    },

    async createInvoice(planId) {
      this.loading = true;
      this.error = null;
      try {
        const response = await subscriptionApi.createInvoice({ planId });
        return response.data;
      } catch (error) {
        this.error = error.message;
        throw error;
      } finally {
        this.loading = false;
      }
    },

    async upgrade(data) {
      this.loading = true;
      this.error = null;
      try {
        const response = await subscriptionApi.upgrade(data);
        this.subscription = response.data;
        //    
        await this.fetchLimits();
        return response.data;
      } catch (error) {
        this.error = error.message;
        throw error;
      } finally {
        this.loading = false;
      }
    }
  }
});
