<template>
  <v-bottom-sheet
    :model-value="modelValue"
    @update:model-value="$emit('update:modelValue', $event)"
  >
    <v-sheet
      :style="{ borderRadius: '24px 24px 0 0' }"
      class="pa-4"
    >
      <div class="text-h6 mb-4">  </div>
      
      <div v-if="loading" class="text-center py-8">
        <v-progress-circular
          indeterminate
          color="primary"
        />
      </div>

      <div v-else class="block-types-grid">
        <v-card
          v-for="blockType in availableBlockTypes"
          :key="blockType.blockType"
          elevation="0"
          class="block-type-card"
          :style="{ borderRadius: '16px' }"
          :class="{ 'locked': !blockType.isFreeBlock && !isPremium }"
          @click="handleSelect(blockType)"
        >
          <v-card-text class="text-center">
            <v-icon
              :size="40"
              :color="blockType.isFreeBlock || isPremium ? 'primary' : 'grey'"
            >
              {{ getBlockIcon(blockType.blockType) }}
            </v-icon>
            <div
              class="text-body-2 mt-2"
              :class="blockType.isFreeBlock || isPremium ? '' : 'text-grey'"
            >
              {{ blockType.blockName }}
            </div>
            <v-icon
              v-if="!blockType.isFreeBlock && !isPremium"
              size="small"
              color="grey"
              class="mt-1"
            >
              mdi-lock
            </v-icon>
          </v-card-text>
        </v-card>
      </div>
    </v-sheet>
  </v-bottom-sheet>

  <!-- Premium Upgrade Dialog -->
  <v-dialog
    v-model="showUpgradeDialog"
    max-width="400"
  >
    <v-card :style="{ borderRadius: '24px' }">
      <v-card-title class="text-h6">
        Premium 
      </v-card-title>
      <v-card-text>
        <p class="text-body-1">
                Premium .
        </p>
        <p class="text-body-2 text-grey mt-2">
           ,      .
        </p>
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <v-btn
          variant="text"
          @click="showUpgradeDialog = false"
        >
          
        </v-btn>
        <v-btn
          color="primary"
          @click="goToSubscription"
        >
            
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script setup>
import { ref, computed, watch, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import { usePagesStore } from '../stores/pages';
import { useSubscriptionStore } from '../stores/subscription';

const props = defineProps({
  modelValue: {
    type: Boolean,
    required: true
  },
  insertOrder: {
    type: Number,
    default: 0
  }
});

const emit = defineEmits(['update:modelValue', 'block-selected']);

const router = useRouter();
const pagesStore = usePagesStore();
const subscriptionStore = useSubscriptionStore();

const loading = ref(false);
const showUpgradeDialog = ref(false);
const selectedBlockType = ref(null);

const isPremium = computed(() => subscriptionStore.isPremium);

const availableBlockTypes = computed(() => {
  return pagesStore.blockTypes.filter(block => {
    //     Premium,    FREE
    return block.isFreeBlock || isPremium.value;
  });
});

const getBlockIcon = (blockType) => {
  const icons = {
    TEXT: 'mdi-text',
    CHECKLIST: 'mdi-format-list-checks',
    IDEA_CARD: 'mdi-lightbulb-on',
    STATUS: 'mdi-flag',
    HEADING: 'mdi-format-header-1',
    QUOTE: 'mdi-format-quote-close',
    DIVIDER: 'mdi-minus',
    LINK: 'mdi-link',
    BULLET_LIST: 'mdi-format-list-bulleted',
    KEY_VALUE: 'mdi-key-variant',
    PROS: 'mdi-thumb-up',
    CONS: 'mdi-thumb-down'
  };
  return icons[blockType] || 'mdi-text-box';
};

const handleSelect = (blockType) => {
  if (!blockType.isFreeBlock && !isPremium.value) {
    selectedBlockType.value = blockType;
    showUpgradeDialog.value = true;
    return;
  }
  
  emit('block-selected', blockType.blockType);
  emit('update:modelValue', false);
};

const goToSubscription = () => {
  showUpgradeDialog.value = false;
  emit('update:modelValue', false);
  router.push('/subscription');
};

watch(() => props.modelValue, async (newVal) => {
  if (newVal) {
    loading.value = true;
    try {
      await pagesStore.fetchBlockTypes();
      await subscriptionStore.fetchSubscription();
    } catch (error) {
      console.error('    :', error);
    } finally {
      loading.value = false;
    }
  }
});

onMounted(async () => {
  if (pagesStore.blockTypes.length === 0) {
    await pagesStore.fetchBlockTypes();
  }
  await subscriptionStore.fetchSubscription();
});
</script>

<style scoped>
.block-types-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 12px;
  max-height: 60vh;
  overflow-y: auto;
  padding-bottom: 16px;
}

.block-type-card {
  cursor: pointer;
  transition: transform 0.2s, opacity 0.2s;
}

.block-type-card:hover:not(.locked) {
  transform: translateY(-4px);
}

.block-type-card.locked {
  opacity: 0.6;
  cursor: not-allowed;
}
</style>
