<template>
  <v-card
    v-if="!editable"
    elevation="0"
    class="bullet-list-block"
    :style="{ borderRadius: '16px' }"
    @click="$emit('edit')"
  >
    <v-card-text>
      <ul v-if="items.length > 0" class="bullet-list">
        <li v-for="(item, idx) in items" :key="idx" class="text-body-1">
          {{ item }}
        </li>
      </ul>
      <div v-else class="text-body-2 text-grey">
         
      </div>
    </v-card-text>
  </v-card>

  <v-card v-else elevation="0" variant="outlined" :style="{ borderRadius: '16px' }">
    <v-card-text>
      <div
        v-for="(item, idx) in localItems"
        :key="idx"
        class="item-edit-row"
      >
        <v-text-field
          v-model="localItems[idx]"
          variant="plain"
          density="compact"
          hide-details
          placeholder=" "
          @keydown.enter="addNewItem"
        />
        <v-btn
          icon
          size="small"
          variant="text"
          @click="removeItem(idx)"
        >
          <v-icon size="small">mdi-close</v-icon>
        </v-btn>
      </div>
      <v-btn
        block
        variant="text"
        prepend-icon="mdi-plus"
        @click="addNewItem"
      >
         
      </v-btn>
      <div class="d-flex justify-end mt-2">
        <v-btn size="small" color="primary" @click="handleSave">
          
        </v-btn>
      </div>
    </v-card-text>
  </v-card>
</template>

<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  block: {
    type: Object,
    required: true
  },
  editable: {
    type: Boolean,
    default: false
  }
});

const emit = defineEmits(['edit', 'save', 'delete']);

const items = props.block.items || [];
const localItems = ref([...items]);

watch(() => props.block.items, (newItems) => {
  localItems.value = newItems ? [...newItems] : [];
}, { deep: true });

const addNewItem = () => {
  localItems.value.push('');
};

const removeItem = (idx) => {
  localItems.value.splice(idx, 1);
};

const handleSave = () => {
  emit('save', {
    items: localItems.value.filter(item => item.trim())
  });
};
</script>

<style scoped>
.bullet-list-block {
  cursor: pointer;
}

.bullet-list {
  padding-left: 20px;
  margin: 0;
}

.item-edit-row {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
}
</style>
