<template>
  <v-dialog
    :model-value="modelValue"
    max-width="400"
    @update:model-value="$emit('update:modelValue', $event)"
  >
    <v-card :style="{ borderRadius: '24px' }">
      <v-card-title class="text-h6">
          
      </v-card-title>
      <v-card-text>
        <v-text-field
          v-model="title"
          label=" "
          variant="outlined"
          density="compact"
          autofocus
          @keydown.enter="handleCreate"
        />
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <v-btn
          variant="text"
          @click="$emit('update:modelValue', false)"
        >
          
        </v-btn>
        <v-btn
          color="primary"
          :loading="loading"
          :disabled="!title.trim()"
          @click="handleCreate"
        >
          
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script setup>
import { ref, watch } from 'vue';
import { usePagesStore } from '../../stores/pages';

const props = defineProps({
  modelValue: {
    type: Boolean,
    required: true
  }
});

const emit = defineEmits(['update:modelValue', 'created']);

const pagesStore = usePagesStore();
const title = ref('');
const loading = ref(false);

watch(() => props.modelValue, (newVal) => {
  if (newVal) {
    title.value = '';
  }
});

const handleCreate = async () => {
  if (!title.value.trim()) return;
  
  loading.value = true;
  try {
    const page = await pagesStore.createPage(title.value.trim());
    emit('created', page);
    emit('update:modelValue', false);
    title.value = '';
  } catch (error) {
    console.error('   :', error);
  } finally {
    loading.value = false;
  }
};
</script>
