import { defineStore } from 'pinia';
import { pagesApi } from '../api/pages';

export const usePagesStore = defineStore('pages', {
  state: () => ({
    pages: [],
    currentPage: null,
    loading: false,
    error: null,
    pagination: {
      page: 0,
      size: 20,
      total: 0
    },
    searchQuery: '',
    blockTypes: []
  }),

  getters: {
    isEmpty: (state) => state.pages.length === 0,
    hasMore: (state) => {
      const { page, size, total } = state.pagination;
      return (page + 1) * size < total;
    }
  },

  actions: {
    async fetchPages(params = {}) {
      this.loading = true;
      this.error = null;
      try {
        const response = await pagesApi.getPages({
          page: this.pagination.page,
          size: this.pagination.size,
          ...params
        });
        this.pages = response.data.content || response.data.pages || [];
        if (response.data.totalElements !== undefined) {
          this.pagination.total = response.data.totalElements;
        }
        return response.data;
      } catch (error) {
        this.error = error.message;
        throw error;
      } finally {
        this.loading = false;
      }
    },

    async searchPages(query, params = {}) {
      this.loading = true;
      this.error = null;
      this.searchQuery = query;
      try {
        const response = await pagesApi.searchPages(query, {
          page: this.pagination.page,
          size: this.pagination.size,
          ...params
        });
        this.pages = response.data.content || response.data.pages || [];
        if (response.data.totalElements !== undefined) {
          this.pagination.total = response.data.totalElements;
        }
        return response.data;
      } catch (error) {
        this.error = error.message;
        throw error;
      } finally {
        this.loading = false;
      }
    },

    async fetchPage(pageId) {
      this.loading = true;
      this.error = null;
      try {
        const response = await pagesApi.getPage(pageId);
        this.currentPage = response.data;
        return response.data;
      } catch (error) {
        this.error = error.message;
        throw error;
      } finally {
        this.loading = false;
      }
    },

    async createPage(title) {
      this.loading = true;
      this.error = null;
      try {
        const response = await pagesApi.createPage({ title });
        this.pages.unshift(response.data);
        return response.data;
      } catch (error) {
        this.error = error.message;
        throw error;
      } finally {
        this.loading = false;
      }
    },

    async updatePage(pageId, data) {
      this.loading = true;
      this.error = null;
      try {
        const response = await pagesApi.updatePage(pageId, data);
        if (this.currentPage?.id === pageId) {
          this.currentPage = response.data;
        }
        const index = this.pages.findIndex(p => p.id === pageId);
        if (index !== -1) {
          this.pages[index] = response.data;
        }
        return response.data;
      } catch (error) {
        this.error = error.message;
        throw error;
      } finally {
        this.loading = false;
      }
    },

    async deletePage(pageId) {
      this.loading = true;
      this.error = null;
      try {
        await pagesApi.deletePage(pageId);
        this.pages = this.pages.filter(p => p.id !== pageId);
        if (this.currentPage?.id === pageId) {
          this.currentPage = null;
        }
      } catch (error) {
        this.error = error.message;
        throw error;
      } finally {
        this.loading = false;
      }
    },

    async fetchBlockTypes() {
      try {
        const response = await pagesApi.getBlockTypes();
        this.blockTypes = response.data.blocks || [];
        return this.blockTypes;
      } catch (error) {
        this.error = error.message;
        throw error;
      }
    },

    async createBlock(pageId, blockData) {
      this.loading = true;
      this.error = null;
      try {
        const response = await pagesApi.createBlock(pageId, blockData);
        if (this.currentPage?.id === pageId) {
          if (!this.currentPage.blocks) {
            this.currentPage.blocks = [];
          }
          this.currentPage.blocks.push(response.data);
          this.currentPage.blocks.sort((a, b) => a.order - b.order);
        }
        return response.data;
      } catch (error) {
        this.error = error.message;
        throw error;
      } finally {
        this.loading = false;
      }
    },

    async updateBlock(pageId, blockId, blockData) {
      this.loading = true;
      this.error = null;
      try {
        const response = await pagesApi.updateBlock(pageId, blockId, blockData);
        if (this.currentPage?.id === pageId && this.currentPage.blocks) {
          const index = this.currentPage.blocks.findIndex(b => b.id === blockId);
          if (index !== -1) {
            this.currentPage.blocks[index] = response.data;
          }
        }
        return response.data;
      } catch (error) {
        this.error = error.message;
        throw error;
      } finally {
        this.loading = false;
      }
    },

    async deleteBlock(pageId, blockId) {
      this.loading = true;
      this.error = null;
      try {
        await pagesApi.deleteBlock(pageId, blockId);
        if (this.currentPage?.id === pageId && this.currentPage.blocks) {
          this.currentPage.blocks = this.currentPage.blocks.filter(b => b.id !== blockId);
        }
      } catch (error) {
        this.error = error.message;
        throw error;
      } finally {
        this.loading = false;
      }
    },

    resetPagination() {
      this.pagination.page = 0;
    },

    nextPage() {
      if (this.hasMore) {
        this.pagination.page++;
      }
    }
  }
});
