import api from './client';

export const pagesApi = {
  //     
  getPages(params = {}) {
    return api.get('/pages', { params });
  },

  //    ID
  getPage(pageId) {
    return api.get(`/pages/${pageId}`);
  },

  //   
  createPage(data) {
    return api.post('/pages', data);
  },

  //  
  updatePage(pageId, data) {
    return api.put(`/pages/${pageId}`, data);
  },

  //  
  deletePage(pageId) {
    return api.delete(`/pages/${pageId}`);
  },

  //  
  searchPages(query, params = {}) {
    return api.get('/pages/search', {
      params: { q: query, ...params }
    });
  },

  //    
  getBlockTypes() {
    return api.get('/pages/block-types');
  },

  //    
  createBlock(pageId, data) {
    return api.post(`/pages/${pageId}/blocks`, data);
  },

  //  
  updateBlock(pageId, blockId, data) {
    return api.put(`/pages/${pageId}/blocks/${blockId}`, data);
  },

  //  
  deleteBlock(pageId, blockId) {
    return api.delete(`/pages/${pageId}/blocks/${blockId}`);
  }
};
