<template>
  <v-dialog
    :model-value="modelValue"
    max-width="400"
    persistent
    @update:model-value="$emit('update:modelValue', $event)"
  >
    <v-card :style="{ borderRadius: '24px' }">
      <v-card-text class="text-center py-8">
        <div class="confetti"></div>
        <h2 class="text-h5 mt-4 mb-2"> !</h2>
        <p class="text-body-1 text-grey">
                 Premium 
        </p>
      </v-card-text>
      <v-card-actions>
        <v-btn
          block
          color="primary"
          size="large"
          @click="handleClose"
        >
           
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script setup>
defineProps({
  modelValue: {
    type: Boolean,
    required: true
  }
});

const emit = defineEmits(['update:modelValue', 'close']);

const handleClose = () => {
  emit('close');
  emit('update:modelValue', false);
};
</script>

<style scoped>
.confetti {
  font-size: 64px;
  animation: bounce 0.5s ease-in-out;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}
</style>
