import axios from 'axios';
import { retrieveLaunchParams } from '@telegram-apps/sdk';

const api = axios.create({
  baseURL: process.env.VUE_APP_API_URL || 'https://owa.my-tg-notes.ru/test/api',
});

api.interceptors.request.use((config) => {
  try {
    let initDataRaw = null;
    
    //    @telegram-apps/sdk
    try {
      const launchParams = retrieveLaunchParams();
      initDataRaw = launchParams?.initDataRaw;
    } catch (e) {
      // Fallback:   window.Telegram.WebApp
      if (window.Telegram?.WebApp?.initData) {
        initDataRaw = window.Telegram.WebApp.initData;
      } else if (window.Telegram?.WebApp?.initDataUnsafe) {
        //  initData ,  initDataUnsafe ( )
        const initData = window.Telegram.WebApp.initDataUnsafe;
        //   initData 
        const params = new URLSearchParams();
        if (initData.user) {
          params.append('user', JSON.stringify(initData.user));
        }
        if (initData.auth_date) {
          params.append('auth_date', initData.auth_date);
        }
        if (initData.hash) {
          params.append('hash', initData.hash);
        }
        initDataRaw = params.toString();
      }
    }
    
    if (initDataRaw) {
      //     Authorization
      //      TelegramAuthFilter
      config.headers.Authorization = `tma ${initDataRaw}`; 
    }
  } catch (e) {
    console.error('   Telegram', e);
  }
  return config;
});

//  
api.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response) {
      const { status } = error.response;
      
      if (status === 401) {
        //  -   
        console.error(' .    Telegram.');
      } else if (status === 403) {
        //   -    
        if (window.location.pathname !== '/subscription') {
          window.location.href = '/subscription';
        }
      } else if (status === 404) {
        //   
        console.error('  ');
      }
    }
    return Promise.reject(error);
  }
);

export default api;
