<template>
  <v-card
    v-if="!editable"
    elevation="0"
    class="checklist-block"
    :style="{ borderRadius: '16px' }"
    @click="$emit('edit')"
  >
    <v-card-text>
      <div v-if="preview && items.length > 0" class="preview-items">
        <div
          v-for="(item, idx) in items.slice(0, 2)"
          :key="idx"
          class="preview-item"
        >
          <v-icon
            size="small"
            :color="item.checked ? 'success' : 'grey'"
          >
            {{ item.checked ? 'mdi-check-circle' : 'mdi-circle-outline' }}
          </v-icon>
          <span class="ml-2 text-body-2">{{ item.text }}</span>
        </div>
      </div>
      <div v-else-if="items.length > 0" class="items-list">
        <div
          v-for="(item, idx) in items"
          :key="idx"
          class="item-row"
        >
          <v-checkbox
            :model-value="item.checked"
            hide-details
            density="compact"
            @click.stop="toggleItem(idx)"
          />
          <span
            class="text-body-1 ml-2"
            :class="{ 'text-decoration-line-through': item.checked }"
          >
            {{ item.text }}
          </span>
        </div>
      </div>
      <div v-else class="text-body-2 text-grey">
         -
      </div>
    </v-card-text>
  </v-card>

  <v-card
    v-else
    elevation="0"
    variant="outlined"
    :style="{ borderRadius: '16px' }"
  >
    <v-card-text>
      <div
        v-for="(item, idx) in localItems"
        :key="idx"
        class="item-edit-row"
      >
        <v-checkbox
          v-model="item.checked"
          hide-details
          density="compact"
        />
        <v-text-field
          v-model="item.text"
          variant="plain"
          density="compact"
          hide-details
          placeholder=" "
          @keydown.enter="addNewItem"
        />
        <v-btn
          icon
          size="small"
          variant="text"
          @click="removeItem(idx)"
        >
          <v-icon size="small">mdi-close</v-icon>
        </v-btn>
      </div>
      <v-btn
        block
        variant="text"
        prepend-icon="mdi-plus"
        @click="addNewItem"
      >
         
      </v-btn>
      <div class="d-flex justify-end mt-2">
        <v-btn
          size="small"
          color="primary"
          @click="handleSave"
        >
          
        </v-btn>
      </div>
    </v-card-text>
  </v-card>
</template>

<script setup>
import { ref, computed, watch } from 'vue';

const props = defineProps({
  block: {
    type: Object,
    required: true
  },
  editable: {
    type: Boolean,
    default: false
  },
  preview: {
    type: Boolean,
    default: false
  }
});

const emit = defineEmits(['edit', 'save', 'delete']);

const items = computed(() => props.block.items || []);
const localItems = ref([...items.value]);

watch(() => props.block.items, (newItems) => {
  localItems.value = newItems ? [...newItems] : [];
}, { deep: true });

const toggleItem = (idx) => {
  if (!props.editable) {
    const updatedItems = [...items.value];
    updatedItems[idx].checked = !updatedItems[idx].checked;
    emit('save', { items: updatedItems });
  }
};

const addNewItem = () => {
  localItems.value.push({ text: '', checked: false });
};

const removeItem = (idx) => {
  localItems.value.splice(idx, 1);
};

const handleSave = () => {
  emit('save', {
    items: localItems.value.filter(item => item.text.trim())
  });
};
</script>

<style scoped>
.checklist-block {
  cursor: pointer;
  transition: background-color 0.2s;
}

.checklist-block:hover {
  background-color: rgba(0, 0, 0, 0.02);
}

.preview-items {
  max-height: 60px;
  overflow: hidden;
}

.preview-item {
  display: flex;
  align-items: center;
  margin-bottom: 4px;
}

.items-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.item-row {
  display: flex;
  align-items: center;
}

.item-edit-row {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
}
</style>
