<template>
  <div v-if="!editable" @click="$emit('edit')">
    <h2 v-if="block.level === 1" class="text-h4 font-weight-bold">
      {{ block.content || ' 1' }}
    </h2>
    <h3 v-else-if="block.level === 2" class="text-h5 font-weight-bold">
      {{ block.content || ' 2' }}
    </h3>
    <h4 v-else class="text-h6 font-weight-bold">
      {{ block.content || ' 3' }}
    </h4>
  </div>

  <v-card v-else elevation="0" variant="outlined" :style="{ borderRadius: '16px' }">
    <v-card-text>
      <v-text-field
        v-model="localContent"
        variant="outlined"
        density="compact"
        label=""
        hide-details
      />
      <v-select
        v-model="localLevel"
        :items="levelOptions"
        label=""
        variant="outlined"
        density="compact"
        class="mt-2"
      />
      <div class="d-flex justify-end mt-2">
        <v-btn size="small" color="primary" @click="handleSave">
          
        </v-btn>
      </div>
    </v-card-text>
  </v-card>
</template>

<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  block: {
    type: Object,
    required: true
  },
  editable: {
    type: Boolean,
    default: false
  }
});

const emit = defineEmits(['edit', 'save', 'delete']);

const localContent = ref(props.block.content || '');
const localLevel = ref(props.block.level || 1);

const levelOptions = [
  { title: ' 1', value: 1 },
  { title: ' 2', value: 2 },
  { title: ' 3', value: 3 }
];

watch(() => props.block.content, (newVal) => {
  localContent.value = newVal || '';
});

watch(() => props.block.level, (newVal) => {
  localLevel.value = newVal || 1;
});

const handleSave = () => {
  emit('save', {
    content: localContent.value,
    level: localLevel.value
  });
};
</script>

<style scoped>
h2, h3, h4 {
  cursor: pointer;
  margin: 16px 0;
}
</style>
