<template>
  <div class="page-editor">
    <!-- Header -->
    <v-app-bar
      elevation="0"
      color="transparent"
      class="px-4"
    >
      <v-text-field
        v-model="pageTitle"
        placeholder=" ..."
        variant="plain"
        hide-details
        class="title-input"
        @blur="saveTitle"
      />
      <v-menu>
        <template #activator="{ props }">
          <v-btn
            icon
            variant="text"
            v-bind="props"
          >
            <v-icon>mdi-dots-vertical</v-icon>
          </v-btn>
        </template>
        <v-list>
          <v-list-item @click="confirmDelete">
            <v-list-item-title> </v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-app-bar>

    <!-- Content -->
    <v-container class="py-4">
      <div v-if="pagesStore.loading" class="text-center py-8">
        <v-progress-circular
          indeterminate
          color="primary"
        />
      </div>

      <div v-else-if="pagesStore.currentPage">
        <div
          v-for="(block, index) in sortedBlocks"
          :key="block.id"
          class="block-container"
        >
          <!-- Block Component -->
          <component
            :is="getBlockComponent(block.type)"
            :block="block"
            :editable="editingBlockId === block.id"
            @edit="startEdit(block.id)"
            @save="saveBlock(block.id, $event)"
            @delete="deleteBlock(block.id)"
          />

          <!-- Add Block Button -->
          <div class="add-block-divider">
            <v-btn
              icon
              size="small"
              variant="text"
              color="grey"
              @click="openBlockTypeSheet(index + 1)"
            >
              <v-icon>mdi-plus</v-icon>
            </v-btn>
          </div>
        </div>

        <!-- Empty State -->
        <div v-if="sortedBlocks.length === 0" class="empty-blocks">
          <v-icon size="60" color="grey-lighten-1">mdi-text-box-outline</v-icon>
          <p class="text-body-2 text-grey mt-4"> +   </p>
        </div>
      </div>
    </v-container>

    <!-- Block Type Sheet -->
    <BlockTypeSheet
      v-model="showBlockTypeSheet"
      :insert-order="insertOrder"
      @block-selected="handleBlockSelected"
    />

    <!-- Confirm Delete Modal -->
    <ConfirmDeleteModal
      v-model="showDeleteModal"
      title=" ?"
      @confirm="handleDelete"
    />
  </div>
</template>

<script setup>
import { ref, computed, onMounted, watch } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { usePagesStore } from '../stores/pages';
import BlockTypeSheet from '../components/BlockTypeSheet.vue';
import ConfirmDeleteModal from '../components/modals/ConfirmDeleteModal.vue';
import TextBlock from '../components/blocks/TextBlock.vue';
import ChecklistBlock from '../components/blocks/ChecklistBlock.vue';
import IdeaCardBlock from '../components/blocks/IdeaCardBlock.vue';
import StatusBlock from '../components/blocks/StatusBlock.vue';
import HeadingBlock from '../components/blocks/HeadingBlock.vue';
import QuoteBlock from '../components/blocks/QuoteBlock.vue';
import DividerBlock from '../components/blocks/DividerBlock.vue';
import LinkBlock from '../components/blocks/LinkBlock.vue';
import BulletListBlock from '../components/blocks/BulletListBlock.vue';
import KeyValueBlock from '../components/blocks/KeyValueBlock.vue';
import ProsConsBlock from '../components/blocks/ProsConsBlock.vue';

const route = useRoute();
const router = useRouter();
const pagesStore = usePagesStore();

const pageId = route.params.pageId;
const pageTitle = ref('');
const editingBlockId = ref(null);
const showBlockTypeSheet = ref(false);
const insertOrder = ref(0);
const showDeleteModal = ref(false);

const sortedBlocks = computed(() => {
  if (!pagesStore.currentPage?.blocks) return [];
  return [...pagesStore.currentPage.blocks].sort((a, b) => a.order - b.order);
});

const getBlockComponent = (blockType) => {
  const components = {
    TEXT: TextBlock,
    CHECKLIST: ChecklistBlock,
    IDEA_CARD: IdeaCardBlock,
    STATUS: StatusBlock,
    HEADING: HeadingBlock,
    QUOTE: QuoteBlock,
    DIVIDER: DividerBlock,
    LINK: LinkBlock,
    BULLET_LIST: BulletListBlock,
    KEY_VALUE: KeyValueBlock,
    PROS: ProsConsBlock,
    CONS: ProsConsBlock
  };
  return components[blockType] || TextBlock;
};

const saveTitle = async () => {
  if (pageTitle.value && pageTitle.value !== pagesStore.currentPage?.title) {
    await pagesStore.updatePage(pageId, { title: pageTitle.value });
  }
};

const startEdit = (blockId) => {
  editingBlockId.value = blockId;
};

const saveBlock = async (blockId, data) => {
  await pagesStore.updateBlock(pageId, blockId, data);
  editingBlockId.value = null;
};

const deleteBlock = async (blockId) => {
  await pagesStore.deleteBlock(pageId, blockId);
};

const openBlockTypeSheet = (order) => {
  insertOrder.value = order;
  showBlockTypeSheet.value = true;
};

const handleBlockSelected = async (blockType) => {
  const blockData = {
    type: blockType,
    order: insertOrder.value
  };
  
  //       
  if (blockType === 'TEXT') {
    blockData.content = '';
  } else if (blockType === 'CHECKLIST') {
    blockData.items = [];
  } else if (blockType === 'IDEA_CARD') {
    blockData.content = '';
  } else if (blockType === 'STATUS') {
    blockData.status = 'TODO';
    blockData.color = 'blue';
  }
  
  await pagesStore.createBlock(pageId, blockData);
  showBlockTypeSheet.value = false;
};

const confirmDelete = () => {
  showDeleteModal.value = true;
};

const handleDelete = async () => {
  await pagesStore.deletePage(pageId);
  router.push('/');
};

onMounted(async () => {
  // Telegram Back Button
  if (window.Telegram?.WebApp?.BackButton) {
    window.Telegram.WebApp.BackButton.show();
    window.Telegram.WebApp.BackButton.onClick(() => {
      router.push('/');
    });
  }
  
  //  
  await pagesStore.fetchPage(pageId);
  if (pagesStore.currentPage) {
    pageTitle.value = pagesStore.currentPage.title || '';
  }
});

watch(() => pagesStore.currentPage, (newPage) => {
  if (newPage) {
    pageTitle.value = newPage.title || '';
  }
});
</script>

<style scoped>
.page-editor {
  min-height: 100vh;
  background-color: var(--tg-theme-bg-color, #F8F9FA);
}

.title-input {
  flex: 1;
  font-size: 1.25rem;
  font-weight: 600;
}

.block-container {
  margin-bottom: 16px;
}

.add-block-divider {
  display: flex;
  justify-content: center;
  padding: 8px 0;
  border-bottom: 1px dashed rgba(0, 0, 0, 0.1);
}

.empty-blocks {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 40vh;
  text-align: center;
}
</style>
