  min-height: 100vh;
  color: var(--tg-theme-text-color, #000000);
}

/*       */
.v-icon {
  color: var(--tg-theme-text-color, inherit) !important;
}

.v-btn .v-icon {
  color: var(--tg-theme-button-text-color, var(--tg-theme-text-color, inherit)) !important;
}

/*     Telegram */
.v-card {
  background-color: var(--tg-theme-secondary-bg-color, var(--tg-theme-bg-color, #FFFFFF)) !important;
  color: var(--tg-theme-text-color, #000000) !important;
}

/*    */
.v-card-title,
.v-card-text {
  color: var(--tg-theme-text-color, #000000) !important;
}

/*   */
.v-field__input {
  color: var(--tg-theme-text-color, #000000) !important;
}

.v-field__prepend-inner .v-icon,
.v-field__append-inner .v-icon {
  color: var(--tg-theme-hint-color, rgba(0, 0, 0, 0.6)) !important;
}

/*    */
.v-chip {
  color: var(--tg-theme-text-color, #000000) !important;
}

/*  */
a {
  color: var(--tg-theme-link-color, #0088cc) !important;
}

/*   placeholder */
.v-field__placeholder,
.v-messages {
  color: var(--tg-theme-hint-color, rgba(0, 0, 0, 0.6)) !important;
}

/*    Telegram */
.v-btn--variant-elevated,
.v-btn--variant-flat {
  background-color: var(--tg-theme-button-color, #0088cc) !important;
  color: var(--tg-theme-button-text-color, #FFFFFF) !important;
}

.v-btn--variant-text .v-icon {
  color: var(--tg-theme-text-color, #000000) !important;
}

/*  */
.v-app-bar {
  background-color: var(--tg-theme-bg-color, #FFFFFF) !important;
  color: var(--tg-theme-text-color, #000000) !important;
}

.v-app-bar .v-icon {
  color: var(--tg-theme-text-color, #000000) !important;
}
</style>
