<template>
  <v-card
    v-if="!editable"
    elevation="0"
    class="link-block"
    :style="{ borderRadius: '16px' }"
    @click="$emit('edit')"
  >
    <v-card-text>
      <div class="d-flex align-center">
        <v-icon class="mr-2">mdi-link</v-icon>
        <a
          :href="block.url"
          target="_blank"
          class="text-body-1 text-primary"
          @click.stop
        >
          {{ block.title || block.url }}
        </a>
      </div>
      <div v-if="block.description" class="text-caption text-grey mt-1">
        {{ block.description }}
      </div>
    </v-card-text>
  </v-card>

  <v-card v-else elevation="0" variant="outlined" :style="{ borderRadius: '16px' }">
    <v-card-text>
      <v-text-field
        v-model="localUrl"
        variant="outlined"
        density="compact"
        label="URL"
        hide-details
      />
      <v-text-field
        v-model="localTitle"
        variant="outlined"
        density="compact"
        label=""
        hide-details
        class="mt-2"
      />
      <v-text-field
        v-model="localDescription"
        variant="outlined"
        density="compact"
        label=" ()"
        hide-details
        class="mt-2"
      />
      <div class="d-flex justify-end mt-2">
        <v-btn size="small" color="primary" @click="handleSave">
          
        </v-btn>
      </div>
    </v-card-text>
  </v-card>
</template>

<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  block: {
    type: Object,
    required: true
  },
  editable: {
    type: Boolean,
    default: false
  }
});

const emit = defineEmits(['edit', 'save', 'delete']);

const localUrl = ref(props.block.url || '');
const localTitle = ref(props.block.title || '');
const localDescription = ref(props.block.description || '');

watch(() => props.block.url, (newVal) => {
  localUrl.value = newVal || '';
});

watch(() => props.block.title, (newVal) => {
  localTitle.value = newVal || '';
});

watch(() => props.block.description, (newVal) => {
  localDescription.value = newVal || '';
});

const handleSave = () => {
  emit('save', {
    url: localUrl.value,
    title: localTitle.value,
    description: localDescription.value
  });
};
</script>

<style scoped>
.link-block {
  cursor: pointer;
}
</style>
