<template>
  <v-card
    v-if="!editable"
    elevation="0"
    class="status-block"
    :style="{ borderRadius: '16px' }"
    @click="$emit('edit')"
  >
    <v-card-text>
      <v-chip
        :color="getColor(block.color || 'blue')"
        size="large"
      >
        {{ getStatusText(block.status) }}
      </v-chip>
    </v-card-text>
  </v-card>

  <v-card
    v-else
    elevation="0"
    variant="outlined"
    :style="{ borderRadius: '16px' }"
  >
    <v-card-text>
      <v-select
        v-model="localStatus"
        :items="statusOptions"
        label=""
        variant="outlined"
        density="compact"
      />
      <v-select
        v-model="localColor"
        :items="colorOptions"
        label=""
        variant="outlined"
        density="compact"
      />
      <div class="d-flex justify-end mt-2">
        <v-btn
          size="small"
          color="primary"
          @click="handleSave"
        >
          
        </v-btn>
      </div>
    </v-card-text>
  </v-card>
</template>

<script setup>
import { ref, watch } from 'vue';

const props = defineProps({
  block: {
    type: Object,
    required: true
  },
  editable: {
    type: Boolean,
    default: false
  }
});

const emit = defineEmits(['edit', 'save', 'delete']);

const localStatus = ref(props.block.status || 'TODO');
const localColor = ref(props.block.color || 'blue');

const statusOptions = [
  { title: ' ', value: 'TODO' },
  { title: ' ', value: 'IN_PROGRESS' },
  { title: '', value: 'DONE' },
  { title: '', value: 'CANCELLED' }
];

const colorOptions = [
  { title: '', value: 'blue' },
  { title: '', value: 'green' },
  { title: '', value: 'orange' },
  { title: '', value: 'red' },
  { title: '', value: 'purple' }
];

const getStatusText = (status) => {
  const option = statusOptions.find(opt => opt.value === status);
  return option ? option.title : status;
};

const getColor = (color) => {
  const colorMap = {
    blue: 'primary',
    green: 'success',
    orange: 'warning',
    red: 'error',
    purple: 'purple'
  };
  return colorMap[color] || 'primary';
};

watch(() => props.block.status, (newVal) => {
  localStatus.value = newVal || 'TODO';
});

watch(() => props.block.color, (newVal) => {
  localColor.value = newVal || 'blue';
});

const handleSave = () => {
  emit('save', {
    status: localStatus.value,
    color: localColor.value
  });
};
</script>

<style scoped>
.status-block {
  cursor: pointer;
}
</style>
